<template>
    <div id="comment">
        <Bar title="所有评论"></Bar>
        <el-table :data="pageData" border style="width: 100%">
            <el-table-column label="ID" width="100">
                <template scope="scope"><span>{{ scope.row.id }}</span></template>
            </el-table-column>
            <el-table-column label="评论者" width="120">
                <template scope="scope"><span>{{ scope.row.nickname }}</span></template>
            </el-table-column>
            <el-table-column label="时间" width="300">
                <template scope="scope">
                    <el-icon name="time"></el-icon>
                    <span>{{ time(scope.row.time) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="内容">
                <template scope="scope"><span>{{ scope.row.content }}</span></template>
            </el-table-column>
            <el-table-column label="操作" width="100">
                <template scope="scope">
                    <el-button size="small" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
          layout="total, prev, pager, next"
          :page-size="100"
          :total="commCount">
        </el-pagination>
    </div>
</template>

<script>
import Bar from './Bar.vue'

export default {
    name: 'Comment',
    data () {
        return {
            pageData: [],
            allComment: {},
        };
    },
    components: { Bar },
    mounted () {
        this.getComment(1);
    },
    computed: {
        commCount: function () { return this.$store.counts.comment; }
    },
    methods: {
        getComment (pageId) {
            if (this.allComment[pageId]) {
                this.pageData = this.allComment[pageId];
                return;
            }
            this.$axios.get('http://127.0.0.1:3000/admin/getcomment/' + (pageId - 1))
                .then((res) => {
                    this.pageData = res.data.result;
                    this.allComment[pageId] = res.data.result;
                })
                .catch((res) => { console.log(res); });
        },
        time (val) {
            return new Date(val).toLocaleString();
        },
        del (index, row) {
            this.$confirm('确定要永久删除该评论吗', '提示', {
                confirmButtonText: '确认删除',
                cancleButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.get('http://127.0.0.1:3000/admin/delcomment/' + row.id)
                    .then(({data}) => {
                        if (data.result) {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.pageData.splice(index, 1);
                            this.$store.updateCount('comment');
                        }
                    }).catch((res) => { console.log(res); });
            }).catch(() => {});
        }
    },
}
</script>

<style>

</style>





