<template>
    <div class="bar">
        <h3 class="title">{{ title }}</h3>
        <p class="desc">{{ message }}</p>
    </div>
</template>

<script>
export default {
    name: 'Bar',
    props: ['title', 'message']
}
</script>

<style scoped>
    .bar {
        margin: 20px 0;
        padding: 10px 0;
        color: #96A4B3;
        font-size: 13px;
        border-bottom: 2px solid #DFE5EB;
    }
    .title, .desc{
        margin: 0;
        line-height: 1.5em;
    }
    .title {
        font-size: 16px;
        color: #5A6B7B;
    }
</style>





